import Vue from "vue";
import Vuex from "vuex";
import router from "../routes.js";
import http from "../../http-common"
import axios from "axios";

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        userinfo:null, //db    ()
        isLogin:false,
        isLoginError:false
    },
    mutations: { //stats   
      //   ,
      loginsuccess(state, payload){
     
        state.isLogin = true
        state.isLoginError = false
    
        state.userinfo = payload
      },
      //   ,
      loginError(state){
        state.isLogin = false
        state.isLoginError = true
      },
      logout(state){
        state.isLogin = false
        state.isLoginError = false
        state.userinfo = null
        sessionStorage.removeItem('access_token')
      },
    }, 
    actions: { //
      // 
      login({dispatch}, loginObj){ // mutations 
         //      .
         let form = new FormData()
         form.append('email', loginObj.email)
         form.append('password',loginObj.password)
         //  -> 
         let str = "email"
         http
          .post("/account/login", form)
          .then(res=>{
            // token:( id )
            //    
            //let token = res.data.token;
 
            if(res.data.data=="noemailcheck") {
                sessionStorage.removeItem('access_token')
                alert("  .");
                router.push({name : "EmailConfirm", params:{email:loginObj.email}})
              } else if(res.data.data!="failed"){ // 
              let token = res.data.data
              //  
              sessionStorage.setItem("access_token", token)
              dispatch("getMemberInfo") //  .
            }else {
                sessionStorage.removeItem('access_token')
              alert("   .")
            }
          })
          
          .catch(error=>{

            alert("  !")
            console.log(error)
          })
      },
      logout({commit}){
  
        commit("logout")
        if(router.history.current.name!=='Login'){
            router.push({name:"Login"})
        }
       
      },
      getMemberInfo({commit}){
          let token = sessionStorage.getItem("access_token")
          
          //     .
          let form = new FormData()
          form.append('access_token', token)
          if(token!=null){
            //       (  )
            //  -> status ->    
                http
                  .post("/account/loginToken", form)
                  .then(response=>{
                      let userinfo = {
                          picture : response.data.object.picture,
                          num : response.data.object.num,
                          name : response.data.object.name,
                          email : response.data.object.email,
                          nickName : response.data.object.nickname,
                          intro : response.data.object.intro,
                        }
                    
                    commit('loginsuccess',userinfo);
                    
                    if(router.history.current.name=='Login'){
                        router.push({name:"MainPage"}).catch((any) =>{});
                    }
                  })
                  .catch(error=>{
                    sessionStorage.removeItem('access_token')
                    alert("  !")
                    console.log(error)
                  })
            
          }
        }
       
    }, 
    modules: {}
});